export const getHtmlCode = ({
  mediaType,
  inputs: {
    apiKey,
    assetsPerPage = 26,
    htmlElement,
    widgetSubtitle,
    widgetTitle,
  },
  booleans: {
    dynamicSubtitle,
    dynamicTitle,
    onItemClick,
    showMore,
    showSearchBar,
  }
}) => {
  const onItemClickString = onItemClick ?
        `
          onItemClick: (e, item) => {
            e.preventDefault();
            console.log(item);
          }` : '';

return `
<html>
  <head>
    <link rel="stylesheet" type="text/css" href="https://api-cdn.shutterstock.com/0.1.15/static/css/sstk-widget.css">
  </head>
  <body>
    <div id="${htmlElement}"></div>
    <script>
      window.onload = () => {
        const widget = new ShutterstockWidget({
          container: document.getElementById('${htmlElement}'),
          dynamicTitle: ${dynamicTitle},
          dynamicSubtitle: ${dynamicSubtitle},
          key: '${apiKey}',
          mediaType: '${mediaType}',
          showMore: ${showMore},
          showSearchBar: ${showSearchBar},
          title: '${widgetTitle}',
          subtitle: '${widgetSubtitle}',
          assetsPerPage: ${assetsPerPage},${onItemClickString}
        });

        widget.search({
          query: ''
        });
      }
    </script>
    <script src="https://api-cdn.shutterstock.com/0.1.15/static/js/sstk-widget.js"></script>
  </body>
</html>
`
};
