export const getReactCode = ({
  mediaType,
  inputs: {
    apiKey,
    assetsPerPage,
    htmlElement,
    widgetSubtitle,
    widgetTitle,
  },
  booleans: {
    dynamicSubtitle,
    dynamicTitle,
    onItemClick,
    showMore,
    showSearchBar,
  },
}) => {
  const onItemClickString = onItemClick ?
        `
      onItemClick: (e, item) => {
          e.preventDefault();
          console.log(item);
        }` : '';

return `
import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';

const SearchWidget = () => {
  const widgetRef = useRef();

  useEffect(() => {
    const widgetConfig = {
      mediaType: '${mediaType}',
      title: '${widgetTitle}',
      subtitle: '${widgetSubtitle}',
      container: widgetRef.current,
      showMore: ${showMore},
      key: '${apiKey}',
      dynamicTitle: ${dynamicTitle},
      dynamicSubtitle: ${dynamicSubtitle},
      showSearchBar: ${showSearchBar},
      assetsPerPage: ${assetsPerPage},${onItemClickString}
    };

    // eslint-disable-next-line no-undef
    if (typeof window === 'object' && window.ShutterstockWidget) {
      // eslint-disable-next-line no-undef
      const widgetInstance = new window.ShutterstockWidget(widgetConfig);
      widgetInstance.search({
        query: '',
      });
      widgetRef.current = widgetInstance;
    }
  }, []);

  return <div ref={widgetRef} />;
};

export default SearchWidget;
`
};
